.App {
  text-align: center;
  font-family: "Courier New";
  background-color: white;
  font-size: 1rem;
}

.header {
  background-color: #2A2F33;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 4rem;
  color: white;
  padding: 1% 0 0.5%;
}

footer {
  background-color: #DAF7A6;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.sample-text-box {
  border: 3px solid black;
  width: 50%;
  padding: 5% 2%;
  margin: 5% auto 0;
  font-size: 2rem;
}

.sample-text-container {
  background-color: white;
  width: 100%;
  height: 100%;
}

.sample-text {
  font-weight: bold;
}

/* User Input */
.user-input {
  margin: 5% auto 0;
}

.user-input textarea {
  border: 0;
  border-bottom: 2px solid black;
  width: 50%;
  padding: 0;
  font-size: 2.5rem;
  overflow: hidden;
  height: 4rem;
}

.user-input textarea:focus {
  outline: none;
}

/* Stats */
#stats-holder {
  display: none;
  width: 60%;
  margin: 0 auto;
}

#stats-holder button {
  margin: 8% 4%;
  padding: 1rem 0.5rem;
  background-color: #EF7C8E;
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
}

/* Icons */
.FaKeyboard {
  height: 150px;
  width: 150px;
}

/* Try Again Animation */

.try-again-btn {
  display: none;
  background-color: green;
  color: white;
  font-weight: bold;
  padding-left: 2rem;
  padding-right: 2rem;
}

.try-again-btn:hover {
  background-color: #DAF7A6;
}

.try-again-btn span {
  display: inline-block;
  animation: wave-text 2s ease-in-out infinite;
  letter-spacing: 2px;
}

.try-again-btn .try-3 {
  margin: 0 8px;
}

@keyframes wave-text{
  00%{
    transform: translateY(0.2rem);
  }
  60%{
    transform: translateY(-0.2rem);
  }
  100%{
    transform: translateY(0.2rem);
  }
}

/* ///////////////////////////////////////////////////////////////////////////////////////////////////// */

 @media only screen and (min-width: 768px) and (max-width: 992px) {

    .header {
      padding: 3% 0 1%;
    }

   .sample-text-box {
     margin: 5% auto 0;
     padding: 8% 2%;
     width: 70%;
   }

   .user-input {
     margin: 7% auto 0;
   }

   .user-input textarea {
     width: 65%;
   }

   #stats-holder {
     width: 85%;
   }

   #stats-holder button {
     margin: 12% 1.25rem;
     padding: 1rem 0rem;
   }

   .try-again-btn {
     padding: 1rem 2rem;
   }
 }

/* ///////////////////////////////////////////////////////////////////////////////////////////////////// */

 @media only screen and (max-width: 767px) {

   .body {
     font-size: 0.75rem;
   }

   header {
     padding: 6% 0 3%;
   }

   h1 {
     font-size: 2rem;
   }

   .sample-text-box {
     margin: 2% auto 0;
     width: 95%;
     padding: 10% 0.5%;
     font-size: 1.75rem;
   }

   .user-input {
     margin: 10% auto 0rem;
   }

   .user-input textarea {
     margin: 0rem auto 0rem;
     width: 90%;
     font-size: 1.75rem;
     height: 2.75rem;
   }

   #stats-holder {
     margin: 8% auto;
     width: 95%;
   }

   #stats-holder button {
     margin: 0.6rem 0rem;
   }

   .try-again-btn {
     padding: 1rem 2rem;
   }

   .FaKeyboard {
     height: 100px;
     width: 100px;
   }

 }
